<template>
  <div class="big_box">
    <!-- 住多久 -->
    <section class="times">
      <p class="howlong">住多久</p>
      <div class="choose_time">
        <div class="left">
          <div>
            {{ now_time }}
            <br />
            <span>现在入住</span>
          </div>
          <img
            src="../../assets/right.png"
            class="img_right"
            alt
          />
          <div>
            {{ end_time }}
            <br />
            <span>{{leaveTime}}前离店</span>
          </div>
        </div>
        <div
          class="right"
          @click="choose"
        >
          {{ num }}
          <span class="unit">晚</span>
          <img
            src="../../assets/c_right.png"
            class="img_unit"
            alt
          />
        </div>
      </div>
    </section>
    <!-- 选择房间 -->
    <p class="title">选择房间</p>
    <div class="scroll_box">
      <section
        class="rooms"
        v-for="(scale,i) in roomsList"
        :key="i"
      >
        <!-- 房型 -->
        <div class="scale_box">
          <div
            v-if="scale.freeNum==0||scale.rooms.length==0"
            class="column_0"
          ></div>
          <div
            v-else
            class="column"
          ></div>
          <div>
            <label>
              {{scale.scaleName}}
              <span
                v-if="scale.freeNum==0||scale.rooms.length==0"
                class="full"
              >暂无空房间</span>
            </label>
            <br />
            <span>
              剩余房间
              <span
                class="scale_num"
                v-if="scale.freeNum==0||scale.rooms.length==0||scale.freeNum<0"
              >0</span>
              <span v-else>{{scale.freeNum>scale.rooms.length?scale.rooms.length:scale.freeNum}}</span>
            </span>
          </div>
        </div>
        <!-- 房间 -->
        <div
          v-if="scale.freeNum==0||scale.rooms.length==0||scale.freeNum<0"
          class="noRoom"
        >暂无空闲房间，或房间正在打扫。</div>
        <section
          class="bottom_room"
          v-else
        >
          <div
            @click="btngo(roomNum,scale.scaleName)"
            class="room_box"
            v-for="(roomNum,i) in scale.rooms"
            :key="i"
            id="colorBox"
          >
            <div>
              <p class="child1">{{roomNum.roomSn}}</p>
            </div>
          </div>
        </section>
      </section>
    </div>
    <!-- 选择时间 -->
    <mt-popup
      v-model="time_box"
      class="showbox"
    >
      <div
        v-for="(day,i) in 10"
        :key="i"
      >
        <div
          class="show1"
          @click="choosed_day(day)"
        >住{{day}}晚</div>
      </div>
    </mt-popup>
  </div>
</template>
<script>
import { GetBusinessRoomscaleHandleRoomByhotel } from '@/api/api_business_hotel_and_scale.js';
export default {
    data() {
        return {
            now_time: '',
            end_time: '',
            num: 1,
            time_box: false,
            roomsList: [],
            leaveTime: ''
        };
    },
    created() {
        this.$store.commit('setHeader', '散客入住');
        this.getRooms();
        this.weeks = ['周日', '周一', '周二', '周三', '周四', '周五', '周六'];
        let nowHours = new Date().getHours();
        if (0 <= nowHours && nowHours < 8) {
            this.lastDay = -1;
            this.now_time = this.GetDateStr(-1);
            this.end_time =
                (new Date().getMonth() + 1 < 10 ? '0' + (new Date().getMonth() + 1) : new Date().getMonth() + 1) + '月' + (new Date().getDate() < 10 ? '0' + new Date().getDate() : new Date().getDate()) + '日' + ' ' + this.weeks[new Date().getDay()];
        } else {
            this.now_time =
                (new Date().getMonth() + 1 < 10 ? '0' + (new Date().getMonth() + 1) : new Date().getMonth() + 1) + '月' + (new Date().getDate() < 10 ? '0' + new Date().getDate() : new Date().getDate()) + '日' + ' ' + this.weeks[new Date().getDay()];
            this.end_time = this.GetDateStr(this.num);
        }
    },
    methods: {
        getRooms() {
            GetBusinessRoomscaleHandleRoomByhotel(this.$route.query.hotelId, { diffDay: this.num }).done((res) => {
                this.roomsList = res.data;
                this.leaveTime = res.order.leaveTime;
            });
        },
        // 获取几天后的时间
        GetDateStrLast(AddDayCount) {
            if (this.lastDay == -1) {
                var dd = new Date();
                dd.setDate(dd.getDate() - 1 + AddDayCount); //获取AddDayCount天后的日期
                var m = dd.getMonth() + 1 < 10 ? '0' + (dd.getMonth() + 1) : dd.getMonth() + 1; //获取当前月份的日期，不足10补0
                var d = dd.getDate() - 1 < 10 ? '0' + dd.getDate() : dd.getDate(); //获取当前几号，不足10补0
                var z = this.weeks[dd.getDay()]; //获取当前几号，不足10补0
                return m + '月' + d + '日' + ' ' + z;
            } else {
                var dd = new Date();
                dd.setDate(dd.getDate() + AddDayCount); //获取AddDayCount天后的日期
                var m = dd.getMonth() + 1 < 10 ? '0' + (dd.getMonth() + 1) : dd.getMonth() + 1; //获取当前月份的日期，不足10补0
                var d = dd.getDate() < 10 ? '0' + dd.getDate() : dd.getDate(); //获取当前几号，不足10补0
                var z = this.weeks[dd.getDay()]; //获取当前几号，不足10补0
                return m + '月' + d + '日' + ' ' + z;
            }
        },
        // 获取几天后的时间
        GetDateStr(AddDayCount) {
            var dd = new Date();
            dd.setDate(dd.getDate() + AddDayCount); //获取AddDayCount天后的日期
            var m = dd.getMonth() + 1 < 10 ? '0' + (dd.getMonth() + 1) : dd.getMonth() + 1; //获取当前月份的日期，不足10补0
            var d = dd.getDate() < 10 ? '0' + dd.getDate() : dd.getDate(); //获取当前几号，不足10补0
            var z = this.weeks[dd.getDay()]; //获取当前几号，不足10补0
            return m + '月' + d + '日' + ' ' + z;
        },
        // 弹出框-选择时间
        choose() {
            this.time_box = true;
        },
        // 选择时间
        choosed_day(day) {
            this.num = day;
            this.end_time = this.GetDateStrLast(day);
            this.getRooms();
            this.time_box = false;
        },
        // 选择房间
        btngo(roomNum, name) {
            sessionStorage.removeItem('handlerRoom');
            sessionStorage.removeItem('radiooo');
            roomNum.now_time = this.now_time;
            roomNum.end_time = this.end_time;
            roomNum.num = this.num;
            roomNum.scaleName = name;
            this.$router.push({
                path: '/quicklive',
                query: {
                    split_info: roomNum
                }
            }),
                localStorage.setItem('split_info', JSON.stringify(roomNum));
        }
    }
};
</script>
<style scoped>
.big_box {
    font-size: 12px;
}
.times {
    text-align: left;
}
.title {
    font-size: 18px;
    font-weight: bold;
    padding: 12px 4px;
    background: #f6f6f6;
    text-align: left;
    color: #000;
}
.scroll_box {
    position: fixed;
    top: 200px;
    bottom: 0;
    left: 0;
    right: 0;
    overflow-y: scroll;
}
.rooms {
    text-align: left;
    background: #fff;
    margin-bottom: 10px;
}
.howlong {
    font-size: 18px;
    font-weight: bold;
    padding: 12px 4px;
    background: #f6f6f6;
    color: #000;
}
.choose_time {
    background: #fff;
    font-size: 12px;
    color: #313443;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 4px 16px 4px 4px;
}
.left {
    color: rgba(49, 52, 67, 1);
    opacity: 0.53;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 58%;
}
.img_right {
    width: 20px;
    height: 20px;
}
.right {
    color: #ff7d41;
    font-weight: bold;
    font-size: 36px;
}
.unit {
    font-weight: normal;
    font-size: 18px;
}
.img_unit {
    width: 16px;
    height: 16px;
}
.scale_box {
    background: #fff;
    padding: 12px 16px 0 12px;
    display: flex;
    font-size: 14px;
    font-weight: bold;
    color: #313443;
    line-height: 20px;
}
.scale_box > div > span {
    font-size: 12px;
    color: #6a6a6a;
    font-weight: normal;
    opacity: 1;
}
.column_0 {
    height: 36px;
    width: 4px;
    background: #b2b2b2;
    margin-right: 4px;
}
.column {
    height: 36px;
    width: 4px;
    background: #02bc6c;
    margin-right: 6px;
}
.full {
    color: #f00;
    font-weight: bold;
    padding-left: 20px;
}
.scale_num {
    color: #02bc6c;
}
.bottom_room {
    display: flex;
    width: 100%;
    text-align: center;
    flex-wrap: wrap;
    padding: 2px 12px 12px 12px;
    box-sizing: border-box;
    justify-content: flex-start;
}
.noRoom {
    font-size: 12px;
    color: #6a6a6a;
    opacity: 1;
    padding: 16px 24px;
}
.room_box {
    text-align: left;
    padding: 10px;
    box-sizing: border-box;
    width: 32%;
    height: 70px;
    background: #f0f0f0;
    margin: 2px;
}
.child1 {
    font-size: 18px;
    font-weight: bold;
    color: rgba(49, 52, 67, 1);
    opacity: 1;
    width: 90%;
    overflow: hidden;
    text-overflow: ellipsis;
}
.child2 {
    color: rgba(136, 136, 136, 1);
    opacity: 1;
}
.child3 {
    color: rgba(49, 52, 67, 1);
    opacity: 1;
}
/* 弹出框的宽度 */
.showbox {
    width: 80%;
    border-radius: 5px;
    padding: 8px 10px;
    color: #313443;
    font-weight: bold;
    font-size: 18px;
}
.show1 {
    line-height: 35px;
    background: #f7f7f7;
    margin: 4px 0;
    letter-spacing: 8px;
}
#colorBox:active {
    background-color: rgba(255, 125, 65, 1);
    opacity: 0.95;
    cursor: pointer;
}
.know_box {
    width: 80%;
    border-radius: 10px;
}
.know_box > p:nth-child(1) {
    color: #000;
    font-size: 22px;
    font-weight: bold;
    padding: 20px;
}
.know_box > p:nth-child(2) {
    text-align: left;
    padding: 0 20px;
    font-size: 14px;
    color: #939393;
    line-height: 30px;
}
.know_box > .knowed {
    width: 100%;
    background: #fff;
    margin-top: 20px;
    color: #ff7d41;
    font-size: 20px;
}
</style>



